import React, {useState, useEffect} from 'react';

export default () => {

    const [items, setItems] = useState([]);

    useEffect(() => {
        fetch(`${process.env.REACT_APP_BASEURL}/citaten`)
            .then(r => r.json())
            .then(setItems);
    }, []);

    return (
        <table>
            <thead>
                <tr>
                    <th>id</th>
                    <th>name</th>
                    <th>spreker</th>
                    <th>categorie</th>
                </tr>
            </thead>
            <tbody>
            {items.map((item, index) =>
                <tr key={index}>
                    <td>{item.id}</td>
                    <td>{item.name}</td>
                    <td>{item.spreker}</td>
                    <td>{item.categorie}</td>
                </tr>
            )}
            </tbody>
        </table>
    )
}